.createAccount-section {
  padding: 20px 0;
  nav {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0 30px;
    border-bottom: 1px solid #9192948f;
    a {
      display: block;
      margin: auto;
      width: 18%;
      @media (max-width: 425px) {
        width: 38%;
      }
    }
  }
  form {
    h2 {
      letter-spacing: 1px;
      text-transform: capitalize;
      font-size: 20px;
      margin-bottom: 20px;
    }
    width: 40%;
    @media (max-width: 768px) {
      width: 100%;
    }
    margin: auto;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    .input-container {
      width: 100%;
      margin-bottom: 20px;
      input {
        width: 100%;
        padding: 10px;
        border: 1px solid #919294;
        border-radius: 10px;
        transition: 0.5s;
        &:focus {
          border-color: #f3a01e;
        }
      }
    }
    .continue {
      width: 100%;
      background-color: #4399d4;
      border: none;
      padding: 7px;
      border-radius: 10px;
      margin-bottom: 15px;
      text-transform: capitalize;
      font-size: 20px;
      color: #fff;
      transition: 0.5s;
      &:hover {
        background-color: #f3a01e;
      }
    }
    .social-login {
      margin-bottom: 10px;
      h4 {
        color: #919294;
        text-transform: capitalize;
        letter-spacing: 1.4px;
        font-size: 14px;
        margin-bottom: 12px;
      }
      button {
        border: none;
        background-color: transparent;
      }
    }
    .have-account {
      font-size: 20px;
      margin-bottom: 0;
      @media (max-width: 375px) {
        font-size: 17px;
      }

      a {
        margin-left: 5px;
        transition: 0.5s;
        color: #4399d4;
        &:hover {
          color: #f3a01e;
        }
        body.ar & {
          margin-right: 5px;
          //   margin-left: unset;
        }
      }
    }
  }
}
